.App {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.profile-circle:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); /* Add shadow effect on hover */
}

.profile-text {
  position: absolute;
  top: 20px; /* Adjust top position */
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px; /* Adjust font size */
  color: #ffffff; /* White color */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Add shadow effect */
  z-index: 3; /* Ensure text is above other elements */
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(12, 49, 86, 0.9) 0%, rgba(10, 1, 110, 0.6) 100%); /* Change background to black-gray gradient */
  opacity: 0.8;
  z-index: 0; /* Ensure background is behind other elements */
}

.container {
  position: relative;
  z-index: 1;
}

.profile-circle, .github-circle, .linkedin-circle {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s; /* Add box-shadow transition */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Hide overflow */
}

.profile-circle img, .github-circle img, .linkedin-circle img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensure the image covers the entire circle */
  border-radius: 50%; /* Apply border radius to image */
}

.profile-circle {
  width: 550px; /* Increase size by 20% */
  height: 550px; /* Increase size by 20% */
  background-color: #ffffff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); /* Add shadow effect */
  z-index: 2; /* Ensure profile circle is above the other circles */
}

.github-circle, .linkedin-circle {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  top: calc(50% - 80px); /* Adjust top position */
}

.github-circle {
  background-color: #333333;
  right: calc(330px); /* Adjust left position */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.linkedin-circle {
  background-color: #0e76a8;
  left: calc(330px); /* Adjust left position */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.github-circle:hover, .linkedin-circle:hover {
  transform: translateY(-10px) scale(1.1); /* Adjust upward animation */
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}
/* Add styles for the name text */
.name-text {
  position: absolute;
  top: 10px; /* Adjust as needed */
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-size: 32px; /* Adjust size as needed */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Shadow effect */
  z-index: 10; /* Ensure it's above other elements */
}
* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100vh;
  background-color: #000;
  overflow: hidden;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.wrapper {
  width: 100%;
  height: 100vh;
  transform-style: preserve-3d;
  perspective: 2000px;
}

.circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #000;
  opacity: 0.8;
  border-radius: 50%;
  animation: zoomout 3s infinite;
}

/* Example circle with specific styles */
.circle-example {
  background-color: hsl(90, 90%, 50%); /* Example color */
  width: 40px;
  height: 40px;
  animation-delay: -1.5s; /* Example delay */
}

@keyframes zoomout {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(0) translate3d(0, 0, -2000px);
    opacity: 0;
  }
}
